#footer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #C3A97E;
    color: #213F84;
    padding: 20px;
    margin-top: 20px;
    bottom: 0;
    left: 0;
}

#media-links {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
    margin-bottom: 15px;
}

.sml {
    width : 50px;
    height : 50px;
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 5px;
}

.sml:hover {
    transform: scale(1.2);
    cursor: pointer;
}

footer > p {
    margin-top: 20px
}

